@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: 'Noto Sans JP', sans-serif;
  background-color: #f6f6f6;
  font-size: 18px;
}

h4 {
  text-align: center;
}

.match {
  background-color: white;
  width: 600px;
  margin-left: auto;
  margin-right: auto;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  padding: 60px;
  height: 1100px;
}

.heading {
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

#referenceID {
  font-size: 12px;
}

.canvasClass {
  width: 200px;
  height: 200px;
}

.canvasClassRecord {
  margin-left: auto;
  margin-right: auto;
  width: 200px;
  height: 200px;
}

.form {
  font-style: italic;  
  font-weight: bold;
}
.formW { 
  color: #00B159;
  display: inline-block;
}
.formL {
  color: #FF3F3F;
  display: inline-block;
}
.formD {
  color: #8A8A8A;
  display: inline-block;
}

.teams  {
  position: relative;
}

.homeTeam {
  position: absolute;
  left: 0;
  text-align: right;
  width: 40%;
}

.awayTeam {
  position: absolute;
  top: 0;
  right: 0;
  text-align: left;
  width: 40%;
}
 

.homeStadiumRecord, .awayStadiumRecord {
  position: absolute;
  text-align: center;
  width: 47%;
  bottom: 355px;
  font-weight: bold;
  font-size: 20px;
}
.homeStadiumRecord {
  left: 0;
}

.awayStadiumRecord {
  right: 0;
}

img {
  width: 100px;
  height: 100px;
}

hr {
  border-top: 1px solid #adadad;
}


.loadingData {
  text-align: center;
}

.spinner.loading {
  border: none;
  position: relative;
  text-align: center;
}

.spinner.loading:before {
  content: "";
  height: 20px;
  width: 20px;
  position: absolute;
  border-width: 4px;
  border-style: solid;
  border-color: #2180c0 #fff #fff;
  border-radius: 100%;
  animation: rotation .7s infinite linear;
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  } to {
    transform: rotate(359deg);
  }
}
